.popup {
  height: 100vh;
  width: 100%;
  z-index: 99999;
  position: fixed;
  top: 0;
  left: 0;
  background-color: rgba($color-black,.8);
  opacity: 0;
  visibility: hidden;
  transition: all .3s;

  @supports (-webkit-backdrop-filter: blur(10px)) or (backdrop-filter: blur(10px)) {
    -webkit-backdrop-filter: blur(10px);
    backdrop-filter: blur(10px);
    background-color: rgba($color-black,.3);
}


  &__content {
    @include absCenter;
    width: 75%;
    background-color: $color-white;
    box-shadow: 0 2rem 4rem rgba($color-black, .2);
    border-radius: 3px;
    display: table;
    overflow: hidden;
    opacity: 0;
    transform: translate(-50%, -50%) scale(.25);
    transition: all  .4s .2s;

    @include respond(tab-land) {
      display: block;
      width: 80%;
    }
  }

  &__left {
    width: 33.333333%;
    display: table-cell;
    padding: 3rem 0 3rem 2rem;

    @include respond(tab-port) {
    display: block;
    width: 100%;
    }

    @include respond(tab-land) {
      padding: 1rem;
    }

  }



  &__right {
    width: 66.6666667%;
    display: table-cell;
    vertical-align: middle;
    padding: 3rem 5rem;


    @include respond(tab-land) {
      width: 95%;
      padding: 0;
      display: block;
      margin: 1rem auto;
    }
  }

  &__btn-center{
    @include respond(tab-land) {
      text-align: center;
    }
  }

  &__img {
    display: block;
    width: 100%;

    @include respond(tab-land) {
      width: 50%;
      float: left;
      height: 194.016px;
      object-fit: cover;

    }

   }




  &__text {
    font-size: 1.4rem;
    margin-bottom: 4rem;

    -moz-column-count: 2;
    -moz-column-gap: 4rem; //default 1em=1.4rem=14px
    -moz-column-rule: 1px solid $color-grey-light-2;

    column-count: 2;
    column-gap: 4rem; //default 1em=1.4rem=14px
    column-rule: 1px solid $color-grey-light-2;

    -moz-hyphens: auto;
    -ms-hyphens: auto;
    -webkit-hyphens: auto;
    hyphens: auto;

    @include respond(tab-land) {
      -moz-column-count: 1;
      column-count: 1;
      margin-bottom: 1rem;
      float: left;
      text-align: left;
      -webkit-hyphens: none;
      hyphens: none;

    }
  }

  &:target {
    opacity: 1;
    visibility: visible;
  }

  &:target &__content {
    opacity: 1;
    transform: translate(-50%, -50%) scale(1);
  }

  &__close {
    &:link,
    &:visited {
      color: $color-grey-dark;
      position: absolute;
      top: 2.5rem;
      right: 2.5rem;
      font-size: 3rem;
      text-decoration: none;
      line-height: 1;
      display: inline-block;
      transition: all .2s;


    }

    &:hover,
    &:active {
      color: $color-primary;
    }
  }



}